@import 'globals';
@import 'variables';

.banner1{
    margin-bottom: 22px;
    margin-top: 32px;
    .wraper-banner{
        .inside-banner{
            padding: 0 48px;
            .text-wrap{
                margin-top: 32px;
                display: flex;
                justify-content: space-between;
                align-items: baseline;
                .cases-text{
                    display: flex;
                    // gap: 12px;
                    h1{
                        margin:0;
                        font-family: 'Defectica-Regular', sans-serif;
                        color: $priamry_color;
                        font-size: 177px;
                        line-height: 212.4px;
                        font-weight: 400;
                    }
                    p{
                        font-family: 'Aeonik-Regular', sans-serif;
                        color: $priamry_color;
                        font-size: 18px;
                        line-height: 25.2px;
                        margin: 0;
                    }
                }

            }
            .scroll{
                text-decoration: none;
                font-family: 'Defectica-Regular', sans-serif;
                color: $color-cat;
                font-size: 60px;
                line-height: 74.4px;
                background: none; 
                border: none; 
                cursor: pointer;
            }
            
            .img-wrap{
                img{
                    width: 100%;
                    max-height: 900px;
                }
            }
        }
    }
}

